function animation()
	{
		var canvas = document.getElementById( "dot-animation" ),
		    ctx = canvas.getContext( '2d' );

		canvas.width = canvas.clientWidth;
	    	canvas.height = canvas.clientHeight;

	    	var nStars, minRadius, radius, maxRadius;

		var starsArr = [], // Array that contains the stars
			FPS = 60, // Frames per second
				mouse = {
		     		x: 0,
		      		y: 0
		    		};  // mouse location

		var centerX = canvas.width / 2;
		var centerY = canvas.height / 2;

		if (canvas.height < 500) {
			nStars = 60;
			minRadius = canvas.height * 0.3;
			radius = canvas.height * 0.4;
			maxRadius = canvas.height * 0.5;

		} else {
			nStars = 80;
			minRadius = canvas.height * 0.15;
			radius = canvas.height * 0.25;
			maxRadius = canvas.height * 0.45;
		}

		var diffRadius = maxRadius - radius;

		// Push stars to array

		for (var i = 0; i < nStars; i++) {
				var angle = Math.random() * Math.PI * 2;

				if ( i < ( nStars * 0.4 )) {
					starsArr.push({
							x: ( Math.cos( angle ) * radius ) + centerX,
							y: ( Math.sin( angle ) * radius ) + centerY,
						});

					} else {

						starsArr.push({
						    		x: ( Math.cos( angle ) * ( radius + ( Math.random() * diffRadius ) ) ) + centerX,
						    		y: ( Math.sin( angle ) * ( radius + ( Math.random() * diffRadius ) ) ) + centerY,
						    		vx: Math.floor( Math.random() * 50 ) - 25,
						    		vy: Math.floor( Math.random() * 50 ) - 25
						  	});
					}
			}

		// Draw the scene

		function draw() {
				ctx.clearRect( 0 ,0, canvas.width, canvas.height );
			  
			  	ctx.globalCompositeOperation = "lighter";
			  
			  	for (var i = 0, x = starsArr.length; i < x; i++) {
				    		var s = starsArr[i];
				  
				    		ctx.fillStyle = "#fff";
				    		ctx.beginPath();
				    		ctx.arc(s.x, s.y, 3, 0, 2 * Math.PI);
				    		ctx.fill();
				    		ctx.fillStyle = "#fff";
				    		ctx.stroke();
				  	}
			  
			  	ctx.beginPath();

			  	for (var i = 0, x = starsArr.length; i < x; i++) {
				    		var starI = starsArr[i];
				    
				    		ctx.moveTo( starI.x,starI.y ); 
				    		
				    		if ( distance( mouse, starI ) < 150 ) ctx.lineTo( mouse.x, mouse.y );

			    			for ( var j = 0, x = starsArr.length; j < x; j++ ) {
				      			var starII = starsArr[j];
				      			
				      			if (distance(starI, starII) < 150) {
					        				//ctx.globalAlpha = (1 / 150 * distance(starI, starII).toFixed(1));
					        				ctx.lineTo(starII.x,starII.y);
					        			}
				      		}
					}

			  	ctx.lineWidth = 0.1;
			  	ctx.strokeStyle = "#fff";
			  	ctx.stroke();
			}

		function distance( point1, point2 ) {
			  	var xs = 0;
			  	var ys = 0;
			 
			  	xs = point2.x - point1.x;
			  	xs = xs * xs;
			 
			  	ys = point2.y - point1.y;
			  	ys = ys * ys;
			 
			 	return Math.sqrt( xs + ys );
			}

		// Update star locations

		function update() {
				var dist, s;

				for ( var i = 0, x = starsArr.length; i < x; i++ ) {
			    		s = starsArr[i];
			  
			  		if (i >= ( nStars * 0.4 )) {
					    		s.x += s.vx / FPS;
					    		s.y += s.vy / FPS;

					    		var distX = s.x - centerX;
					    		var distY = s.y - centerY;

					   		dist = Math.sqrt( (distX * distX) + ( distY * distY ) );

					   	}
			    		//console.log( dist );

			    		if (i >= ( nStars * 0.4 ) && i < ( nStars * 0.5 )) {
					    		if ( dist < minRadius || dist > maxRadius ) {
						    			s.vx = -s.vx;
						    			s.vy = -s.vy;
						    		}

						} else if (i >= ( nStars * 0.5 )) {
							if (dist > maxRadius ) {
						    			s.vx = -s.vx;
						    			s.vy = -s.vy;
						    		}
						}

			    		//if (s.x < 0 || s.x > canvas.width) s.vx = -s.vx;
			    		//if (s.y < 0 || s.y > canvas.height) s.vy = -s.vy;
			  	}
			}

		canvas.addEventListener('mousemove', function(e){
			 	mouse.x = e.clientX;
			  	mouse.y = e.clientY;
			});

		// Update and draw

		function tick() {
				draw();
			  	update();
			  	requestAnimationFrame(tick);
			}

		tick();
	}

function feature()
{
		var canvases = document.getElementsByClassName( 'canvas-feature' );
		var imgdata;

		var contexts = [];
			
			for( var i = 0; i < canvases.length; i++ ) {
				contexts[i] = canvases[i].getContext( '2d' );  //initialize each canvas with context.
			}

			function drawToCanvas() {
					// Do your drawing on canvases[1];
					canvases[1].width = canvases[1].clientWidth;
					canvases[1].height = canvases[1].clientHeight;
					contexts[1].beginPath();
					contexts[1].strokeStyle = "#E85A4F";

					if (Foundation.MediaQuery.upTo('small')) {
					 	contexts[1].moveTo(40, 80);
						contexts[1].lineTo(40, 0);
					
					} else if (Foundation.MediaQuery.atLeast('medium')) {
					 	contexts[1].moveTo(16, 80);
						contexts[1].lineTo(64, 0);
					}

					contexts[1].stroke();

					paintToAllContexts();
			}

		function paintToAllContexts() {
					for( var i = 2; i < canvases.length; i++ ) {
						canvases[i].width = canvases[i].clientWidth;
						canvases[i].height = canvases[i].clientHeight;
						contexts[i].drawImage( canvases[1], 0, 0 );
					}

					canvases[0].width = canvases[1].clientWidth / 2;
					canvases[0].height = canvases[1].clientHeight / 2;
					contexts[0].beginPath();
					contexts[0].strokeStyle = "#E85A4F";
					
					if (Foundation.MediaQuery.upTo('small')) {
					 	contexts[0].moveTo(40, 40);
					 	contexts[0].lineTo(40, 0);
					 	contexts[0].lineTo(80, 0);

					
					} else if (Foundation.MediaQuery.atLeast('medium')) {
					 	contexts[0].moveTo(16, 40);
					 	contexts[0].lineTo(40, 0);
					 	contexts[0].lineTo(80, 0);

					}

					contexts[0].stroke();
			}

		drawToCanvas();
	}

jQuery(document).ready( function() {
	//
	jQuery(document).on('click', function(e)
		{
			jQuery('#main-navigation').find('li').find('ul').css('display', "none");
		});

	jQuery('[data-toggle="sub-nav"]').on('mouseover', function(e)
		{
			e.preventDefault();
			e.stopPropagation();

			jQuery('#main-navigation').find('li').find('ul').css('display', "none");

			jQuery(this).closest('li').find('ul').css('display', "block");
		});

	jQuery('[data-toggle="off-canvas"]').on('click', function(e)
	{
		e.stopPropagation();

	  	if(jQuery(this).hasClass('is-activated')) {
	    		jQuery('#off-canvas').foundation('close');

	    		jQuery(this).removeClass('is-activated');

	  	} else {
	    		jQuery('#off-canvas').foundation('open');

	    		jQuery(this).addClass('is-activated');
	  	}
	});

	jQuery('#off-canvas').on('close.zf.offCanvas', function()
	{
	  	jQuery('[data-toggle="off-canvas"]').removeClass('is-activated');
	});

	if (jQuery('.has-dot-animation').length > 0) {
		animation();

		window.addEventListener("resize", animation);
	}

	if (jQuery('.has-key-feature').length > 0) {
		feature();

		window.addEventListener("resize", feature);
	}
	
	if (jQuery('#brand-carousel').length > 0) {
		jQuery('#brand-carousel').slick(
			{
				'dots': true,
				'appendDots': jQuery('#brand-carousel-dots-container'),
				'arrows': false,
			});
	}
});